.diceWrap {
  position: absolute;
  width: 200px;
  height: 200px;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
}

.diceWrap::before {
  position: absolute;
  content: "";
  width: 70%;
  height: 10%;
  top: 90%;
  left: 15%;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 100%;
  filter: blur(10px);
}

.diceFace {
  box-sizing: border-box;
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: rgba(250, 0, 0, 0.45);
  border: 2px solid rgba(250, 0, 0, 0.45);
  border-radius: 30px;
  transform-style: preserve-3d;
  transition: 0.5s;
}

.diceFace::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(250, 0, 0, 0.45);
  border-radius: 20px;
  transform: translateZ(-1px);
}

.diceFace::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -10px;
  background-color: #fff;
  border-radius: 100%;
  transform: translateZ(1px);
}

.front {
  transform: translateZ(50px);
}

.front::after {
  width: 40px;
  height: 40px;
  margin: -20px 0 0 -20px;
  background-color: #fff;
}

.up {
  transform: rotateX(90deg) translateZ(50px);
}

.up::after {
  margin: -30px 0 0 -30px;
  box-shadow: 40px 40px #fff;
}

.left {
  transform: rotateY(-90deg) translateZ(50px);
}

.left::after {
  margin: -40px 0 0 -40px;
  box-shadow: 30px 30px #fff, 60px 60px #fff;
}

.right {
  transform: rotateY(90deg) translateZ(50px);
}
.right::after {
  margin: -30px 0 0 -30px;
  background-color: #fff;
  box-shadow: 40px 0px #fff, 0px 40px #fff, 40px 40px #fff;
}

.bottom {
  transform: rotateX(-90deg) translateZ(50px);
}
.bottom::after {
  margin: -36px 0 0 -36px;
  box-shadow: 26px 26px #fff, 52px 52px #fff, 52px 0px #fff,
    0px 52px #fff;
}

.back {
  transform: rotateX(180deg) translateZ(50px);
}

.back::after {
  margin: -40px 0 0 -30px;
  box-shadow: 40px 0px #fff, 0px 30px #fff, 40px 30px #fff,
    0px 60px #fff, 40px 60px #fff;
}

.dice {
  position: absolute;
  width: 100px;
  height: 100px;
  top: calc(50% - 50px);
  left: calc(50% - 50px);
  transform-style: preserve-3d;
  transform: rotate3d(0, 0.9, 0.9, 90deg);
  transition: 0.5s cubic-bezier(0.42, 1.57, 0.62, 0.86);
}

.decorators {
  height: 100vh;
  position: absolute;
  width: 100vw;
  z-index: -1;
  display: flex;
  justify-content: space-around;
}

.decorator {
  width: 1px;
  background-color: #d4d1d1;
}